import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent} from 'react'
import s from './InputText.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    setError?: (error: string) => void
    error?: string
    label?: string
    spanClassName?: string
}

export const InputText: React.FC<SuperInputTextPropsType> = (
    {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter, setError,
        label, error,
        className,

        ...restProps//       restProps
    }
) => {

    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        setError && setError('')
        onChange && onChange(e) //    onChange     ( onChange  )
        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);
        onEnter && e.key === 'Enter' && onEnter() //    onEnter     Enter,   
    }
    const onBlurcallback = (e: React.FocusEvent<HTMLInputElement>) => {
        setError && setError('')
        if (!e.currentTarget.value) {
            setError && setError('field is required')
        }
    }

    const finalInputClassName = `${error ? s.errorInput : ''} ${s.input} ${className}`


    return (
        <div className={s.wrapper}>
            <input
                type={'text'}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                onBlur={onBlurcallback}
                className={finalInputClassName}
                required //   label!!!

                {...restProps} //        (value   )

            />
            <span className={s.animationBorder}> </span>
            <label className={s.label}>{label}</label>
        </div>
    )
} // TODO: required?