import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes} from 'react';
import s from './ToggleCheckBox.module.css';

type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>
type SuperCheckboxPropsType = DefaultInputPropsType & {
    onChangeChecked?: (checked: boolean) => void
    spanClassName?: string
    checked?: boolean
}

export const ToggleCheckBox: React.FC<SuperCheckboxPropsType> = (
    {
        type, //          
        onChange, onChangeChecked,
        className, spanClassName,
        children, checked,//     ,         React.FC

        ...restProps//       restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {

        onChange && onChange(e);
        onChangeChecked && onChangeChecked(e.currentTarget.checked);
        // && onChangeChecked(e.currentTarget.checked)
        //     onChange  onChangeChecked
    };

    const finalInputClassName = `${s.superInput} ${className ? className : ''}`;
    return (
        <label className={s.checkbox}>
            <input
                type={'checkbox'}
                onChange={onChangeCallback}
                className={finalInputClassName}

                {...restProps} //        (checked   )
            />
            <span className={!checked ? s.checkboxSwitch : s.checkboxSwitchChecked} {...restProps}/>
            {children && <span className={s.spanClassName}>{children}</span>}
        </label> //  label      
    );
};

